<template>
  <div class="guideClass">
    <RouterView />
  </div>
</template>

<script>
import Store from '@/store'
import { getSearch } from '@/utils'
const mapJson = {
  A: 'GroupA',
  B: 'GroupBC',
  C: 'GroupBC'
}
export default {
  async beforeRouteEnter (to, from, next) {
    const orderN = getSearch().order_num || localStorage.getItem('guide_order_num')
    const phone = getSearch().phone_id || localStorage.getItem('guide_phone')
    // 没有订单号则不分组
    if (!orderN) return next()
    // 获取abc 测试信息 并跳转指定页面
    const data = await Store.dispatch('ABTest/getAbData', { domain: 'guide', abKey: 'mini_abc_fenban', factor: orderN, whiteParam: phone })
    const routerName = mapJson[data.ab_group] || 'GroupA'
    if (to.name === routerName) {
      return next()
    }
    next({ name: routerName, query: to.query })
  }
}
</script>
